import axios from 'axios'
import JSONbig from 'json-bigint'
import http from '@/utils/myhttp.js'

const instance = axios.create({
  // `transformResponse` 在传递给 then/catch 前，允许修改响应数据
  transformResponse: [function (data) {
    try {
      // 对 data 进行任意转换处理
      return JSONbig.parse(data)
    } catch {
      return data
    }
  }]
})

// 获取某频道文章列表
export function apiGetarticles (id) {
  return instance({
    url: 'http://ttapi.research.itcast.cn/app/v1_1/articles',
    params: {
      channel_id: id,
      timestamp: Date.now(),
      with_top: 0
    }
  })
}
// 对文章不感兴趣
export function apiDislikes (id) {
  return http({
    method: 'post',
    url: '/article/dislikes',
    data: {
      target: id
    }
  })
}
// 取消对文章不感兴趣
export function apiOffDislikes (id) {
  return http({
    method: 'DELETE',
    url: '/article/dislikes/' + id
  })
}
// 举报文章
export function apireports (obj) {
  return http({
    method: 'post',
    url: '/article/reports',
    data: obj
  })
}
// 拉黑文章
export function apiBlacklists (id) {
  return http({
    method: 'post',
    url: '/user/blacklists',
    data: {
      target: id
    }
  })
}

// 文章详情
export function apiDetails (id) {
  return http({
    url: '/articles/' + id
  })
}
// 对文章点赞
export function apiLikings (id) {
  return http({
    method: 'post',
    url: '/article/likings',
    data: {
      target: id
    }
  })
}
// 取消对文章点赞
export function apiOffLikings (id) {
  return http({
    method: 'DELETE',
    url: '/article/likings/' + id
  })
}
