<template>
  <div class="homepage">
    <van-nav-bar title="标题" />
    <van-tabs class="tab" v-model="active">
      <van-tab v-for="(item,index) in channels" :key="index" :title="item.name">
        <!-- 下拉刷新 -->
        <van-pull-refresh v-model="item.isLoading" @refresh="onRefresh">
          <!-- 丄拉加载 -->
          <van-list
            v-model="item.loading"
            :finished="item.finished"
            finished-text="没有更多了"
            @load="onLoad"
            :offset="50"
          >
            <van-cell
              v-for="(subitem,subindex) in item.articleList"
              :key="subindex"
              @click="xq(subitem)"
            >
              <template #title>
                <b>{{subitem.title}}</b>
                <van-grid :border="false" :column-num="3">
                  <van-grid-item v-for="(imgurl,imgindex) in subitem.cover.images" :key="imgindex">
                    <van-image :src="imgurl" lazy-load />
                  </van-grid-item>
                </van-grid>
                <van-cell title="单元格" class="dncell">
                  <template #title>
                    <span>{{subitem.aut_name}}</span>
                    <span>{{subitem.comm_count}}条评论</span>
                    <span>{{subitem.pubdate | mydate}}</span>
                  </template>
                  <template #right-icon>
                    <van-icon name="cross" @click.stop="xx(subitem,subindex)" />
                  </template>
                </van-cell>
              </template>
            </van-cell>
          </van-list>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
    <!-- 编辑频道组件 -->
    <van-icon name="wap-nav" class="iconbtn" @click.self="compile">
      <compile ref="compile" :active="active" @input="v=>active=v" :channels="channels"></compile>
    </van-icon>
    <!-- 更多设置组件 -->
    <more ref="more" :val="val" :num="num" @del="del"></more>
  </div>
</template>

<script>
import { apiGetChannel } from '@/api/channel.js'
import { apiGetarticles } from '@/api/articles.js'
import compile from './com/compile'
import more from './com/more'
// 懒加载
import Vue from 'vue'
import { Lazyload } from 'vant'
Vue.use(Lazyload)

export default {
  data () {
    return {
      falg: false,
      // 频道列表
      channels: [],
      // tab当前页
      active: 0,
      // 传给更多组件的值
      val: '',
      num: ''
    }
  },
  components: {
    compile,
    more
  },
  methods: {
    // 下拉加载
    onLoad () {
      const id = this.channels[this.active].id
      apiGetarticles(id).then(res => {
        if (res.data.data.results.length === 0) {
          this.channels[this.active].finished = true
        } else {
          this.channels[this.active].articleList = [...this.channels[this.active].articleList, ...res.data.data.results]
          // this.$nextTick(() => {
          this.channels[this.active].loading = false
          // })
        }
      })
    },
    // 刷新
    onRefresh () {
      this.channels[this.active].articleList = []
      this.onLoad()
      this.$nextTick(() => {
        this.channels[this.active].isLoading = false
        this.onLoad()
      })
    },
    // 获取频道列表
    async getChannels () {
      const res = await apiGetChannel()
      this.channels = res.data.data.channels
      this.channels.forEach(element => {
        this.$set(element, 'articleList', [])
        // 上拉加载
        this.$set(element, 'loading', false)
        this.$set(element, 'finished', false)
        // 下拉刷新
        this.$set(element, 'isLoading', false)
      })
    },
    // 打开编辑频道
    compile () {
      this.$refs.compile.show = true
    },
    // 打开更多操作
    xx (val, num) {
      this.val = val
      this.num = num
      this.$refs.more.show = true
    },
    del () {
      this.channels[this.active].articleList.splice(this.num, 1)
    },
    // 跳到详情
    xq (subitem) {
      console.log(subitem)
      this.$router.push('/details?id=' + subitem.art_id)
    }
  },
  created () {
    if (!this.falg) {
      const token = this.$store.state.user.token
      try {
        if (token) {
          // 已经登录
          this.getChannels()
        } else {
          // 未登录
          const localChannels = localStorage.getItem('channels')
          if (localChannels) {
            // 曾经浏览过
            this.channels = JSON.parse(localChannels)
          } else {
            // 未登录且不曾浏览过
            this.getChannels()
          }
        }
      } catch {
        this.$toast.fail('获取失败')
      }
      this.falg = true
    }
  }
}
</script>

<style lang="less">
.homepage {
  .van-nav-bar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #1383ff;
    z-index: 999;
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .tab {
    .van-tabs__wrap {
      position: fixed;
      top: 46px;
      width: 90%;
      z-index: 999;
    }
    .van-tabs__content {
      margin: 90px 0 50px;
    }
    .van-tab--active {
      color: #f00;
      font-weight: 700;
    }
  }
  .iconbtn {
    position: fixed;
    width: 10%;
    height: 44px;
    top: 46px;
    right: 0;
    z-index: 9999;
    background: hsla(282, 19%, 27%, 0.3);
    text-align: center;
    line-height: 44px;
    font-size: 24px;
  }
  .iconbtn::before {
    color: #fff;
  }
  .dncell {
    padding: 0;
    display: flex;
    align-items: center;
    span {
      font-size: 12px;
      color: #687;
      margin-right: 5px;
    }
  }
  .van-grid-item {
    min-height: 30px;
  }

  .van-grid-item__content {
    padding: 5px 3px;
  }
}
</style>
