<template>
  <div class="userInfo">
    <van-nav-bar
      title="个人信息"
      right-text="保存"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
      class="title"
    />
    <van-cell-group>
      <van-cell class="pop" @click="openUpload">
        <template #title>
          <div>头像</div>
          <div class="img">
            <van-image round width="40px" height="40px" :src="userInfo.photo" />
            <van-icon name="arrow" />
          </div>
        </template>
      </van-cell>
      <van-cell @click="showName=true" title="昵称" is-link :value="userInfo.name" />
      <van-cell @click="showIntro=true" title="介绍" is-link :value="userInfo.intro" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="性别" @click="showGender=true" is-link :value="userInfo.gender===0?'男':'女'" />
      <van-cell title="生日" @click="showItem=true" is-link :value="userInfo.birthday" />
    </van-cell-group>
    <!-- 修改 -->
    <van-popup v-model="showName" position="bottom" :style="{ height: '8%' }">
      <van-field v-model="userInfo.name" placeholder="请输入昵称" required />
    </van-popup>
    <van-popup v-model="showIntro" position="bottom" :style="{ height: '10%' }">
      <van-field v-model="userInfo.intro" placeholder="请输入介绍" type="textarea" />
    </van-popup>
    <van-popup v-model="showGender" position="bottom" :style="{ height: '40%' }">
      <van-picker
        show-toolbar
        title="性别"
        :columns="columns"
        @cancel="onCancel"
        @confirm="onConfirm"
        :default-index="userInfo.gender"
      />
    </van-popup>
    <van-popup v-model="showItem" position="bottom" :style="{ height: '50%' }">
      <van-datetime-picker v-model="currentDate" type="date" @cancel="onCancel" @confirm="onitem" />
    </van-popup>
    <!-- 上传头像组件 -->
    <upload ref="upload"></upload>
  </div>
</template>

<script>
import { apiUser, apiUserProfile, apiUserCompile } from '@/api/user'
import { formattingTime } from '@/utils/mydate'
import upload from './com/upload'
export default {
  data () {
    return {
      userInfo: {
        photo: ''
      },
      showName: false,
      showIntro: false,
      showGender: false,
      columns: ['男', '女'],
      showItem: false,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date()
    }
  },
  components: {
    upload
  },
  methods: {
    // 返回
    onClickLeft () {
      this.$router.back()
    },
    // 保存
    async onClickRight () {
      try {
        await apiUserCompile({
          name: this.userInfo.name,
          birthday: this.userInfo.birthday,
          intro: this.userInfo.intro,
          gender: this.userInfo.gender
        })
        this.$toast.success('保存成功')
      } catch (error) {
        this.$toast.fail('保存失败')
      }
    },
    // 打开上传图片组件
    openUpload () {
      this.$refs.upload.show = true
    },
    // 修改性别
    onConfirm (value, index) {
      this.userInfo.gender = index
      this.showGender = false
    },
    // 取消性别、时间弹框
    onCancel () {
      this.showGender = false
      this.showItem = false
    },
    // 修改时间
    onitem (value) {
      const time = formattingTime(value)
      this.userInfo.birthday = time
      this.showItem = false
    }
  },
  async created () {
    const res1 = await apiUser()
    const res2 = await apiUserProfile()
    this.userInfo = { ...res1.data.data, ...res2.data.data }
    console.log(this.userInfo)
    this.currentDate = new Date(this.userInfo.birthday)
  }
}
</script>
<style lang="less">
.userInfo {
  background: #f8f6f8;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  .title {
    background: #3598fe;
    .van-icon,
    .van-nav-bar__text,
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .van-cell-group {
    margin-bottom: 10px;
    .pop {
      .van-cell__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .img {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .van-icon-arrow::before {
            font-size: 16px;
            color: #969799;
          }
        }
      }
    }
  }
}
</style>
