<template>
  <div class="reply">
    <van-popup :value="value" @input="offReply" position="bottom" :style="{ height: '65%' }">
      <!-- 当前评论 -->
      <van-cell>
        <template #title>
          <span>当前评论</span>
        </template>
      </van-cell>
      <comlist :item="item" :isReply='true'></comlist>
      <!-- 评论的回复 -->
      <van-cell>
        <template #title>
          <span>评论的回复</span>
        </template>
      </van-cell>
      <van-list
        class="comlists"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
      <!-- 评论的回复d的内容 -->
        <comlist :item="replyItem" v-for="(replyItem, index) in replys" :key="index"></comlist>
      </van-list>
      <cominp :item="item" @addReply="addReply" :isReply='true'></cominp>
    </van-popup>
  </div>
</template>

<script>
import comlist from './com-list'
import cominp from './com-input.vue'
import bus from '@/utils/mybus'
import { apiComments } from '@/api/comment'
export default {
  props: ['value'],
  data () {
    return {
      item: '',
      loading: false,
      finished: false,
      // 当前页
      offset: null,
      // 页容量
      limit: 10,
      replys: []
    }
  },
  methods: {
    offReply () {
      this.$emit('input', false)
    },
    // 获取评论数据
    async onLoad () {
      const res = await apiComments({
        type: 'c',
        source: this.item.com_id.toString(),
        offset: this.offset,
        limit: this.limit
      })
      this.replys = [...this.replys, ...res.data.data.results]
      this.loading = false
      this.offset = res.data.data.last_id
      if (res.data.data.last_id === res.data.data.end_id) {
        this.finished = true
      }
    },
    addReply (val) {
      this.replys.unshift(val)
    }
  },
  components: {
    comlist,
    cominp
  },
  created () {
    bus.$on('passItem', item => {
      this.item = item
      this.loading = false
      this.finished = false
      // 当前页
      this.offset = null
      // 页容量
      this.limit = 10
      this.replys = []
    })
  }
}
</script>

<style lang='less'>
.reply {
}
</style>
