<template>
  <div class="details">
    <van-nav-bar title="文章详情" left-arrow @click-left="onClickLeft" @click-right="onClickRight">
      <template #right>
        <van-icon name="ellipsis" size="18" />
      </template>
    </van-nav-bar>
    <h2 class="title">{{details.title}}</h2>
    <!-- 作者 -->
    <van-sticky>
      <div class="zuozhe">
        <div class="img">
          <van-image round width="50px" height="50px" :src="details.aut_photo" />
        </div>
        <div class="time">
          <div>{{details.aut_name}}</div>
          <div>{{details.pubdate|mydate}}</div>
        </div>
        <div class="guanzhu">
          <van-button type="info" @click="followed(details)">
            <van-icon name="plus" v-if="!details.is_followed">{{' '}}关注</van-icon>
            <van-icon v-else>取关</van-icon>
          </van-button>
        </div>
      </div>
    </van-sticky>
    <!-- 文章内容 -->
    <div class="essay">
      <van-cell>
        <template #title>
          <div v-html="details.content"></div>
        </template>
      </van-cell>
    </div>
    <!-- 点赞、不喜欢 -->
    <div class="nolike">
      <van-button
        icon="like"
        plain
        round
        @click="isLiking(details)"
        :class="{red:details.attitude==1}"
      >点赞</van-button>
      <van-button
        icon="delete"
        plain
        round
        :class="{gray:details.attitude==0}"
        @click="isLikeEssay(details)"
      >不喜欢</van-button>
    </div>
    <!-- 评论列表 -->
    <van-list
      class="comlists"
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <comlist :item="item" @openRelply="v=>show=v" v-for="(item, index) in comments" :key="index"></comlist>
    </van-list>
    <!-- 评论框 -->
    <cominp @addcoment="addcoment"></cominp>
    <!-- 回复评论 -->
    <reply v-model="show"></reply>
  </div>
</template>

<script>
import comlist from './com/com-list'
import cominp from './com/com-input.vue'
import reply from './com/reply.vue'
import { apifollowings, apicallOff } from '@/api/user'
import { apiDetails, apiLikings, apiOffLikings, apiOffDislikes, apiDislikes } from '@/api/articles'
import { apiComments } from '@/api/comment'
export default {
  data () {
    return {
      // 文章详情
      details: [],
      id: this.$route.query.id, // 文章id
      loading: false,
      finished: false,
      // 当前页
      offset: null,
      // 页容量
      limit: 10,
      comments: [],
      show: false
    }
  },
  components: {
    comlist,
    cominp,
    reply
  },
  methods: {
    // 返回
    onClickLeft () {
      this.$router.back()
    },
    // 更多
    onClickRight () {
    },
    // 获取评论数据
    async onLoad () {
      const res = await apiComments({
        type: 'a',
        source: this.id,
        offset: this.offset,
        limit: this.limit
      })
      this.comments = [...this.comments, ...res.data.data.results]
      this.loading = false
      this.offset = res.data.data.last_id
      if (res.data.data.last_id === res.data.data.end_id) {
        this.finished = true
      }
    },
    // 关注
    followed (details) {
      if (details.is_followed) {
        // 已关注---取消关注
        apicallOff(details.aut_id)
        this.details.is_followed = false
      } else {
        // 未关注---关注
        apifollowings(details.aut_id).then(res => {
          this.details.is_followed = true
        })
      }
    },
    // 文章点赞
    isLiking (details) {
      if (details.attitude === 1) {
        apiOffLikings(details.art_id)
        details.attitude = -1
      } else {
        apiLikings(details.art_id).then(res => {
          details.attitude = 1
        })
      }
    },
    // 不喜欢、取消不喜欢文章
    isLikeEssay (details) {
      if (details.attitude === 0) {
        apiOffDislikes(details.art_id)
        details.attitude = -1
      } else {
        apiDislikes(details.art_id).then(res => {
          details.attitude = 0
        })
      }
    },
    // 对文章评论
    addcoment (arr) {
      this.comments.unshift(arr)
    }
  },
  created () {
    // 获取文章详情
    apiDetails(this.id).then(res => {
      this.details = res.data.data
    })
  }
}
</script>

<style lang='less'>
.details {
  .van-nav-bar {
    background: #1989fa;
    .van-icon,
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .title {
    text-align: center;
    margin: 20px 10px;
  }
  .zuozhe {
    padding: 10px 15px;
    display: flex;
    align-items: center;
    background: #fff;
    .time {
      flex: 1;
      margin: 0 10px;
      color: #888;
      font-size: 14px;
    }
  }
  .nolike {
    padding: 10px 20px;
    display: flex;
    justify-content: space-around;
  }
  .comlists {
    margin-bottom: 54px;
  }
  .red {
    color: red;
  }
  .gray {
    color: gray;
  }
}
</style>
