import http from '@/utils/myhttp.js'
// 联想
export function apiSuggestion (val) {
  return http({
    url: '/suggestion',
    method: 'get',
    params: {
      q: val
    }
  })
}
// 获取搜索结果
export function apiSearch (obj) {
  return http({
    url: '/search',
    method: 'get',
    params: obj
  })
}
