<template>
  <div class="zhi">
    <van-nav-bar title="小智" fixed left-arrow @click-left="onClickLeft" class="title" />
    <div class="chat" ref="chatbox">
      <div
        class="item"
        :class="item.isuser?'user':'robot'"
        v-for="(item, index) in chatArr"
        :key="index"
      >
        <van-image
          fit='cover'
          class="img"
          round
          width="40px"
          height="40px"
          :src="item.isuser?user.photo:robot"
        />
        <div class="msg">{{item.content}}</div>
      </div>
      <!-- 机器人 -->
      <!-- <div class="robot item">
        <van-image
          class="img"
          round
          width="40px"
          height="40px"
          src="https://img.yzcdn.cn/vant/cat.jpeg"
        />
        <div class="msg">asfsfsfgkkfffsas</div>
      </div>-->
      <!-- 用户 -->
      <!-- <div class="item user">
        <van-image
          class="img"
          round
          width="40px"
          height="40px"
          src="https://img.yzcdn.cn/vant/cat.jpeg"
        />
        <div class="msg">asfsfsfgkkfffsas</div>
      </div>-->
    </div>
    <van-search
      class="value"
      v-model="value"
      left-icon
      show-action
      placeholder="请输入..."
      @search="onSearch"
      background="#4fc08d"
    >
      <template #action>
        <div @click="onSearch">发送</div>
      </template>
    </van-search>
  </div>
</template>

<script>
import io from 'socket.io-client'
import { apiUser } from '@/api/user'
export default {
  data () {
    return {
      user: {},
      robot: 'https://img.yzcdn.cn/vant/cat.jpeg',
      value: '',
      chatArr: [
        { content: 'abc', isuser: false },
        { content: 'abc', isuser: false },
        { content: 'abc', isuser: true },
        { content: 'abc', isuser: false }
      ],
      socket: null
    }
  },
  methods: {
    onClickLeft () {
      this.$router.back()
    },
    onSearch () {
      if (this.value.trim() === '') {
        return
      }
      this.chatArr.push({ content: this.value, isuser: true })
      this.$nextTick(() => {
        var chatbox = this.$refs.chatbox
        chatbox.scrollTop = chatbox.scrollHeight
      })
      this.socket.emit('message', {
        msg: this.value,
        timestamp: Date.now()
      })
      this.value = ''
    }
  },
  created () {
    apiUser().then(res => {
      this.user = res.data.data
    })
    const token = this.$store.state.user.token
    this.socket = io('http://ttapi.research.itcast.cn?token=' + token)
    this.socket.on('message', data => {
      this.chatArr.push({ content: data.msg, isuser: false })
      this.$nextTick(() => {
        var chatbox = this.$refs.chatbox
        chatbox.scrollTop = chatbox.scrollHeight
      })
    })
  }
}
</script>
<style lang="less">
.zhi {
  .title {
    background: #3598fe;
    .van-icon,
    .van-nav-bar__text,
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .chat {
    position: fixed;
    top: 46px;
    left: 0;
    right: 0;
    bottom: 54px;
    background: #eed;
    overflow: auto;
    .item {
      margin: 10px 0;
      padding: 10px;
      // background: #fff;
      overflow: hidden;
      .msg {
        padding: 10px;
        background: lightblue;
        border-radius: 4px;
        max-width: 75%;
      }
    }
    .user {
      .img,
      .msg {
        float: right;
        margin-left: 10px;
      }
    }
    .robot {
      .img,
      .msg {
        float: left;
        margin-right: 10px;
      }
    }
  }
  .value {
    position: fixed;
    width: 100%;
    bottom: 0;
    .van-search__action:active {
      background: #4fc08d;
    }
    .van-search__action {
      color: #fff;
    }
  }
}
</style>
