<template>
  <div class="more">
    <van-popup v-model="show" :style="{ width: '90%' }">
      <van-cell-group class="listicon" v-if="isInform">
        <van-cell @click="dislikes">
          <template #title>
            <van-icon name="failure" class="ii" />不感兴趣
          </template>
        </van-cell>
        <van-cell is-link @click.stop="isInform=false">
          <template #title>
            <van-icon name="warning-o" class="ii" />反馈垃圾内容
          </template>
        </van-cell>
        <van-cell @click="blacklists">
          <template #title>
            <van-icon name="delete" class="ii" />拉黑作者
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group v-else>
        <van-cell>
          <template #title>
            <van-icon name="arrow-left" @click.stop="isInform=true" />
          </template>
        </van-cell>
        <van-cell
          v-for="(item, index) in inform"
          :key="index"
          :title="item.type"
          @click.stop="reports(item.id)"
        ></van-cell>
      </van-cell-group>
    </van-popup>
  </div>
</template>

<script>
import { apiDislikes, apireports, apiBlacklists } from '@/api/articles.js'
export default {
  data () {
    return {
      show: false,
      // 举报类型：
      inform: [
        { id: 1, type: '标题夸张' },
        { id: 2, type: '低俗色情' },
        { id: 3, type: '错别字多' },
        { id: 4, type: '旧闻重复' },
        { id: 5, type: '广告软文' },
        { id: 6, type: '内容不实' },
        { id: 7, type: '涉嫌违法犯罪' },
        { id: 8, type: '侵权' },
        { id: 0, type: '其他问题' }
      ],
      isInform: true
    }
  },
  props: ['val', 'num'],
  methods: {
    // 不感兴趣
    dislikes () {
      try {
        apiDislikes(this.val.art_id.toString()).then(res => {
          if (res.data.message === 'OK') {
            this.$emit('del')
            this.show = false
          }
        })
      } catch {
        this.$toast.fail('操作失败')
      }
    },
    // 举报反馈
    reports (id) {
      try {
        apireports({
          target: this.val.art_id.toString(),
          type: id,
          remark: ''
        }).then(res => {
          console.log(res)
          if (res.data.message === 'OK') {
            this.show = false
            this.isInform = true
            this.$toast.success('反馈成功')
          }
        })
      } catch {
        this.$toast.fail('操作失败')
      }
    },
    // 拉黑
    blacklists () {
      try {
        apiBlacklists(this.val.aut_id).then(res => {
          if (res.data.message === 'OK') {
            this.$toast.success('拉黑成功')
            this.show = false
          }
        })
      } catch {
        this.$toast.fail('操作失败')
      }
    }
  }
}
</script>

<style lang="less">
.more {
  .listicon {
    .ii {
      margin-right: 10px;
      transform: translateY(2px);
    }
  }
}
</style>
