import http from '@/utils/myhttp.js'
// 用户频道列表
export function apiGetChannel () {
  return http({
    url: '/user/channels'
  })
}

// 编辑用户频道列表
export function apiRedactChannel (arr) {
  return http({
    method: 'put',
    url: '/user/channels',
    data: {
      channels: arr
    }
  })
}
// 关注用户
export function apifollowings (id) {
  return http({
    method: 'post',
    url: '/user/followings',
    data: {
      target: id
    }
  })
}
// 取消关注
export function apicallOff (id) {
  return http({
    method: 'DELETE',
    url: '/user/followings/' + id
  })
}
// 个人信息
export function apiUser () {
  return http({
    url: '/user'
  })
}
// 个人资料
export function apiUserProfile () {
  return http({
    url: '/user/profile'
  })
}
// 编辑用户个人资料
export function apiUserCompile (obj) {
  return http({
    method: 'PATCH',
    url: '/user/profile',
    data: obj
  })
}
// 编辑用户照片资料（头像、身份证照片）
export function apiUserPhoto (obj) {
  var fd = new FormData()
  fd.append('photo', obj)
  return http({
    method: 'PATCH',
    url: '/user/photo',
    data: fd
  })
}
