<template>
  <div class="search">
    <van-search
      @input="think"
      v-model="value"
      show-action
      placeholder="请输入搜索关键词"
      @search="onSearch"
      @cancel="onCancel"
    />
    <!-- 联想区域 -->
    <van-cell-group v-if="options.length!==0">
      <van-cell
        icon="search"
        v-for="(item, index) in options"
        :key="index"
        @click="onSearch(item.oldItem)"
      >
        <template #title>
          <span v-html="item.newItem"></span>
        </template>
      </van-cell>
    </van-cell-group>
    <!-- 历史记录 -->
    <van-cell-group v-else-if="history.length!==0" icon="location-o">
      <template #title>
        <div>历史记录</div>
        <div>
          <van-icon name="delete" @click="delAll" />
        </div>
      </template>
      <van-cell v-for="(item, index) in history" :key="index" :title="item" @click="onSearch(item)">
        <template #right-icon>
          <van-icon name="cross" style="line-height: inherit;" @click.stop="del(index)" />
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import { apiSuggestion } from '@/api/search.js'
import { saveData, getData, delData } from '@/utils/mydata.js'
export default {
  data () {
    return {
      value: '',
      // 联想数组
      options: [],
      // 防止抖动
      timer: null,
      // 搜索历史
      history: getData('history') || []
    }
  },
  methods: {
    // 联想
    think () {
      if (this.value.trim() === '') {
        this.options = []
        return
      }
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        apiSuggestion(this.value).then(res => {
          this.options = res.data.data.options
          this.options = this.options.map(element => {
            return {
              oldItem: element,
              newItem: element.replace(this.value, `<b style='color:red'>${this.value}</b>`)
            }
          })
        })
      }, 300)
    },
    // 搜索
    onSearch (val) {
      this.history.unshift(val)
      this.history = [...new Set(this.history)]
      saveData('history', this.history)
      this.$router.push('/searchResult?key=' + val)
    },
    // 取消
    onCancel () {
      this.value = ''
    },
    // 删除当前历史记录
    del (index) {
      this.history.splice(index, 1)
      saveData('history', this.history)
    },
    // 删除所有历史记录
    delAll () {
      this.history = []
      delData('history')
    }
  }
}
</script>

<style lang='less'>
.search {
  .van-search--show-action {
    background: #1383ff;
    .van-search__action {
      color: #fff;
    }
  }
  .van-cell-group__title {
    display: flex;
    justify-content: space-between;
  }
}
</style>
