<template>
  <div class="com-input">
    <van-search v-model="value" left-icon="edit" show-action placeholder="请输入内容" @search="onSearch">
      <template #action>
        <div @click="onSearch">发送</div>
      </template>
    </van-search>
  </div>
</template>

<script>
import { apiAddComment } from '@/api/comment'
export default {
  props: ['isReply', 'item'],
  data () {
    return {
      value: ''
    }
  },
  methods: {
    async onSearch () {
      if (this.value.trim() === '') {
        return
      }
      if (this.isReply) {
        const res = await apiAddComment({
          target: this.item.com_id,
          content: this.value,
          art_id: this.$route.query.id
        })
        this.$emit('addReply', res.data.data.new_obj)
        this.value = ''
        this.item.reply_count++
      } else {
        const res = await apiAddComment({
          target: this.$route.query.id,
          content: this.value
        })
        this.$emit('addcoment', res.data.data.new_obj)
        this.value = ''
      }
    }
  }
}
</script>

<style lang='less'>
.com-input {
  position: fixed;
  width: 100%;
  bottom: 0;
  .van-search--show-action {
    background: #934500;
  }
  .van-search__action {
    color: #fff;
  }
  .van-search__action:active {
    background: #934500;
  }
}
</style>
