<template>
  <div class="searchResult">
    <van-nav-bar fixed title="标题" left-text left-arrow @click-left="onClickLeft" />
    <!-- 丄拉加载 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :offset="50"
      class="pgdn"
    >
      <van-cell v-for="(item, index) in results" :key="index">
        <template #title>
          <b @click="goDetails(item)">{{item.title}}</b>
          <van-grid :border="false" :column-num="3">
            <van-grid-item v-for="(imgurl,imgindex) in item.cover.images" :key="imgindex">
              <van-image :src="imgurl" lazy-load />
            </van-grid-item>
          </van-grid>
          <van-cell class="dncell">
            <template #title>
              <span>{{item.aut_name}}</span>
              <span>{{item.comm_count}}条评论</span>
              <span>{{item.pubdate | mydate}}</span>
            </template>
          </van-cell>
          <van-grid :border="false" :column-num="3" class="sange">
            <van-grid-item @click="comment(item)">
              <template #text>
                <i class="iconfont icon-huifu"></i>
                <span>{{item.comm_count}}</span>
              </template>
            </van-grid-item>
            <van-grid-item @click="like(item)">
              <template #text>
                <i class="iconfont icon-xin-o"></i>
                <span>点赞</span>
              </template>
            </van-grid-item>
            <son abc="abc" @input="abc"></son>
            <input type="text" v-on="$listeners">
            <van-grid-item @click="share(item)" >
              <template #text>
                <i class="iconfont icon-fenxiang"></i>
                <span>分享</span>
              </template>
            </van-grid-item>
          </van-grid>
        </template>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { apiSearch } from '@/api/search.js'
export default {
  data () {
    return {
      loading: false,
      finished: false,
      key: this.$route.query.key,
      page: 0,
      results: []
    }
  },
  inheritAttrs: true,
  methods: {
    // 返回
    onClickLeft () {
      window.history.back()
    },
    onLoad () {
      this.page++
      apiSearch({
        page: this.page,
        q: this.key
      }).then(res => {
        if (res.data.data.results.length !== 0) {
          console.log(res)
          this.results = [...this.results, ...res.data.data.results]
          this.loading = false
        } else {
          this.finished = true
        }
      })
    },
    // 评论
    comment (item) {
      if (this.$login()) {
        console.log(item)
      }
    },
    // 点赞
    like (item) {
      if (this.$login()) {
        console.log(item)
      }
    },
    // 分享
    share (item) {
      if (this.$login()) {
        console.log(item)
      }
    },
    // 跳转文章详情
    goDetails (item) {
      this.$router.push('/details?id=' + item.art_id)
    }
  }
}
</script>

<style lang='less'>
.searchResult {
  .van-nav-bar--fixed {
    background: #1989fa;
  }
  .van-nav-bar__title,
  .van-nav-bar .van-icon {
    color: #fff;
  }
  .pgdn {
    margin-top: 46px;
    .dncell {
      padding: 10px 0;
      display: flex;
      align-items: center;
      span {
        font-size: 12px;
        color: #687;
        margin-right: 15px;
      }
    }
    .van-grid-item__content--center {
      padding: 10px 1px;
    }
    .sange {
      .van-grid-item__content {
        flex-direction: row;
        color: #888;
        i {
          margin-right: 5px;
        }
      }
    }
    .van-grid-item {
      min-height: 30px;
    }
  }
}
</style>
