import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: JSON.parse(window.localStorage.getItem('userInof')) || {},
    userInfo: ''
  },
  mutations: {
    save (state, data) {
      state.user = data
    }
  },
  actions: {},
  modules: {}
})
