<template>
  <div class="compile">
    <van-popup v-model="show" round position="bottom" :style="{ height: '85%' }">
      <br />
      <van-cell title="我的频道">
        <template #right-icon>
          <van-button plain round type="danger" @click="btn=!btn" size="mini" v-if="btn">编辑</van-button>
          <van-button plain round type="danger" @click="btn=!btn" size="mini" v-else>完成</van-button>
        </template>
      </van-cell>
      <van-grid>
        <van-grid-item
          v-for="(item, index) in channels"
          :key="index"
          class="fad"
          @click="tab(index)"
        >
          <template #text>
            <p :class="{'red':index==active}">{{item.name}}</p>
            <van-icon
              name="clear"
              v-if="!btn&&item.id!=0&&channels.length>2"
              class="icon"
              @click.stop="del(item,index)"
            />
          </template>
        </van-grid-item>
      </van-grid>
      <!-- ---------------------- -->
      <van-cell title="推荐频道" />
      <van-grid>
        <van-grid-item v-for="(item, num) in getMsg" :key="num" class="fad" @click="add(item)">
          <template #text>{{item.name}}</template>
        </van-grid-item>
      </van-grid>
    </van-popup>
  </div>
</template>

<script>
import { apiGetAllChannel, apiRedactChannel } from '@/api/channel.js'
export default {
  // apiRedactChannel
  props: ['active', 'channels'],
  data () {
    return {
      show: false,
      btn: true,
      allchannels: []
    }
  },
  methods: {
    // 添加频道
    async add (val) {
      this.$set(val, 'articleList', [])
      // 上拉加载
      this.$set(val, 'loading', false)
      this.$set(val, 'finished', false)
      // 下拉刷新
      this.$set(val, 'isLoading', false)
      this.channels.push(val)
      const token = this.$store.state.user.token
      const arr = this.channels.slice(1).map((item, index) => {
        return {
          id: item.id,
          seq: index + 1
        }
      })
      try {
        if (!token) {
          // 未登录
          localStorage.setItem('channels', JSON.stringify(this.channels))
        } else {
          await apiRedactChannel(arr)
        }
      } catch {
        this.$toast.fail('操作失败')
      }
    },
    // 删除频道
    async del (val, index) {
      console.log(val, index)
      this.channels.splice(index, 1)
      const arr = this.channels.slice(1).map((item, index) => {
        return {
          id: item.id,
          seq: index + 1
        }
      })
      try {
        const token = this.$store.state.user.token
        if (!token) {
          // 未登录
          localStorage.setItem('channels', JSON.stringify(this.channels))
        } else {
          await apiRedactChannel(arr)
        }
      } catch {
        this.$toast.fail('操作失败')
      }
    },
    // 点击频道
    tab (v) {
      this.show = false
      this.$emit('input', v)
    }
  },
  computed: {
    getMsg () {
      const arr = this.channels.map(item => {
        return item.id
      })
      const newarr = this.allchannels.filter(value => {
        return !arr.includes(value.id)
      })
      return newarr
    }
  },
  created () {
    apiGetAllChannel().then(res => {
      this.allchannels = res.data.data.channels
    })
  }
}
</script>

<style lang='less'>
.compile {
  .van-cell__title {
    text-align: left;
  }
  .van-grid-item {
    height: 50px;
  }
  .van-grid-item__content--center {
    line-height: 16px;
  }
  .fad {
    position: relative;
    font-size: 14px;
    color: #555;
    .icon {
      position: absolute;
      top: 2px;
      right: 10px;
    }
  }
  .red {
    color: red;
  }
}
</style>
