<template>
  <div class="index">
    <van-tabbar v-model="active">
      <van-tabbar-item to="/index/homepage" icon="home-o">主页</van-tabbar-item>
      <van-tabbar-item to="/index/search" icon="search">搜索</van-tabbar-item>
      <van-tabbar-item to="/index/video" icon="tv-o">视频</van-tabbar-item>
      <van-tabbar-item to="/index/user" icon="manager-o">我的</van-tabbar-item>
    </van-tabbar>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      active: ''
    }
  },
  created () {
    const arr = ['homepage', 'search', 'video', 'user']
    const path = this.$route.path.split('/')[2]
    arr.forEach((item, index) => {
      if (item === path) {
        this.active = index
      }
    })
  }
}
</script>

