<template>
  <div class="login">
    <van-nav-bar title="登录" />
    <van-form @submit="onSubmit">
      <van-cell-group>
        <van-field
          v-model="obj.mobile"
          placeholder="请输入手机号"
          :rules="[{ validator, required: true, message: '请填写正确手机号' }]"
        >
          <template #left-icon>
            <i class="iconfont icon-iphone2"></i>
          </template>
        </van-field>
        <van-field v-model="obj.code" placeholder="请输入验证码" class="codebtn">
          <template #button>
            <van-button size="small" round color="#efedef" native-type="button">发送验证码</van-button>
          </template>
          <template #left-icon>
            <i class="iconfont icon-lock"></i>
          </template>
        </van-field>
      </van-cell-group>
      <div class="loginbtn">
        <van-button type="info" size="large" :loading="circle">登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { apilogin } from '@/api/login.js'
export default {
  data () {
    return {
      obj: {
        mobile: '18611111111',
        code: '246810'
      },
      circle: false
    }
  },
  methods: {
    validator (val) {
      return /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/.test(
        val
      )
    },
    async onSubmit () {
      this.circle = true
      try {
        const res = await apilogin(this.obj)
        this.$store.commit('save', res.data.data)
        window.localStorage.setItem('userInof', JSON.stringify(res.data.data))
        if (this.$route.path === '/login') {
          this.$router.push('/index/homepage')
        } else {
          this.$router.back()
        }
      } catch {
        this.$toast.fail('登录失败')
      }
      this.circle = false
    }
  }
}
</script>
<style lang="less">
.login {
  .van-nav-bar {
    background: #1383ff;
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .codebtn {
    .van-button--round {
      color: #888 !important;
    }
  }
  .loginbtn {
    margin: 30px 20px;
  }
}
</style>
