<template>
  <div class="com-list">
    <div class="com-img">
      <van-image round width="50px" height="50px" :src="item.aut_photo" />
    </div>
    <div class="comment">
      <div class="writer">{{item.aut_name}}</div>
      <div class="details">{{item.content}}</div>
      <div>
        <span>{{item.pubdate|mydate}}</span>
        <span class="btn" @click="openRelply" v-if="!isReply">回复{{item.reply_count}}</span>
      </div>
    </div>
    <div class="dianzan">
      <van-icon size="22" name="like" :class="{red:item.is_liking}" />
      <div class="num">{{item.like_count}}</div>
    </div>
  </div>
</template>

<script>
import bus from '@/utils/mybus'
export default {
  props: ['item', 'isReply'],
  methods: {
    // 打开回复窗口
    openRelply () {
      this.$emit('openRelply', true)
      bus.$emit('passItem', this.item)
    }
  }
}
</script>

