  padding: 10px 15px;
  .red {
    color: red;
  }
  .comment {
    flex: 1;
    margin-left: 10px;
    color: #565556;
    .writer {
      color: #b4bad1;
    }
    .details {
      margin: 10px 15px;
    }
    .btn {
      padding: 2px 15px;
      background: #f2f5f6;
      border-radius: 100px;
      margin-left: 15px;
    }
    span {
      font-size: 14px;
    }
  }
  .dianzan {
    display: flex;
    .num {
      margin-left: 5px;
    }
  }
}
</style>
