<template>
  <div class="upload">
    <van-popup v-model="show" :style="{ width: '90%' }" :close-on-click-overlay="false">
      <van-cell>
        <template #title>
          <div @click="openfile">从图片中选择</div>
          <input type="file" name ref="file" @change="change" id style="display:none" />
        </template>
      </van-cell>
      <van-cell title="拍照" />
      <van-cell title="取消" @click="show=false" />
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue'
import { ImagePreview } from 'vant'

import { apiUserPhoto } from '@/api/user'
// 全局注册
Vue.use(ImagePreview)
export default {
  data () {
    return {
      show: false,
      url: ''
    }
  },
  methods: {
    openfile () {
      this.$refs.file.click()
    },
    change (file) {
      var data = this
      var iconFile = this.$refs.file.files[0]
      var isImg = iconFile.type.includes('image')
      if (isImg) {
        this.url = URL.createObjectURL(iconFile)
        ImagePreview({
          images: [URL.createObjectURL(iconFile)],
          onClose () {
            // async await
            this.$dialog.confirm({
              message: '将图片设置为头像'
            }).then(() => {
              data.up(iconFile)
            }).catch(() => {
              console.log('取消')
            })
          }
        })
      } else {
        this.$toast.fail('请选择图片')
      }
    },
    // 上传头像
    async up (file) {
      try {
        const res = await apiUserPhoto(file)
        this.$parent.userInfo.photo = res.data.data.photo
        this.show = false
      } catch {
        this.$toast.fail('上传失败')
      }
    }
  },
  created () {
    console.log(this.$parent.userInfo)
  }
}
</script>

<style lang='less'>
.upload {
  text-align: center;
}
</style>
